#resumeButton{
  border-radius: 12%;
}

body{
  padding: 50px 0px 0px 0px;
  background-color: #171C28;
  color: #cac8c8;
}

.jumbotron{
  margin: 0px auto;
  padding: 70px 30px;
  background-color: #171C28;
  color: #cac8c8;
}

#myexperience{
margin-top: 20px;
}

.skill-row{
margin-top: 30px;
}

#skills{
  margin-top: 30px;
}

#myname{
font-size: 300%;
}

#about{
  margin-top: auto;
}

#logo{
  width: 60px;
  height: 50px;
}

.carousel-item > img { 
  width: 100%; 
}

.myCardBody{
  background-color: #dad8d8;
  color: #111111;
}

.myCardImg{
  height: 500px;
}

#projectIcon{
  background-color: #dad8d8;
}

.icon-resize{
  height: 48px;
  width: 48px;
}

footer {
  padding: 50px 0;
}
.copyright {
  padding-top: 20px;
}
.copyright p {
  margin: 0;
  color: #74808a;
}
.top {
  text-align: center;
}
.top span {
  cursor: pointer;
  display: block;
  margin: 15px auto 0 auto;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: 3px solid #b9bfc4;
  text-align: center;
}
.top i {
  color: #74808a;
}
.social {
  text-align: right;
}
.social ul {
  margin: 5px 0 0 0;
  padding: 0;
}
.social li {
  display: inline-block;
  font-size: 1.25em;
  list-style: none;
}
.social a {
  display: block;
  color: #74808a;
  padding: 10px;
  position: relative;
  transition-duration: 0.2s;
}

.social a:hover {
  color: #3498db;
  top: -5px;
  transition-duration: 0.2s;
}

.social-contact{
  text-align: center;
  margin-top: 5%;
}

.social-contact ul {
  margin: 5px 5px 5px 5px;
  padding: 0;
}
.social-contact li {
  display: block;
  font-size: 1em;
  list-style: none;
}

.social-contact a{
  display: block;
  color: #74808a;
  padding: 10px;
  position: relative;
  transition-duration: 0.2s;
}

.social-contact a:hover {
  color: #3498db;
  top: -5px;
  transition-duration: 0.2s;
}

.social-contact p{
  font-weight: 200;
  font-size: 150%;
}

.social-contact i{
  font-size: 125%;
}

.myNav {
  width: 100%;
  padding: 10px;
  position: fixed;
  top: 0;
  transition: top 0.6s;
  z-index: 100;
  background-color: #343a40;
}

.navbar--hidden {
  top: -80px;
}

#emoji{
  font-size: 80%;
}

#contact{
  margin-top: 20px;
}

#edu{
  margin-top: 20px;
}

#work{
    margin-top: 30px;
}

#projects{
  margin-top: 20px;
}

#acad{
  margin-top: 20px;
}

html {
  scroll-behavior: smooth;
}